import React, { useEffect, useState } from 'react';

import './App.css';
import Topbar from './Components/Topbar/Topbar';
import Sidebar from './Components/Sidebar/sidebar';
import Card from './Components/Card/card';
import LoadingAnim from './Components/LoadingAnim/LoadingAnim';

interface ApiResult {
    title: string,
    author: string,
    _id: string,
    chapter: number,
    partA: string,
    partB: string,
    partC: string
}

function App() {
    const [apiData, setApiData] = useState<ApiResult|null>(null);

    useEffect(() => {
        fetch("https://2yve4oenwd.execute-api.us-east-1.amazonaws.com/reads/random")
            .then(blob => blob.json())
            .then(data => setApiData(data));
    }, []);

    return <>
        <Topbar title="The CCCP"/>
        <div className="featured-card-wrapper" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
            {
                apiData
                ? <Card
                    concept={apiData.title}
                    author={apiData.author}
                    favorited={false}
                    partA={apiData.partA}
                    partB={apiData.partB}
                    partC={apiData.partC}
                    _id={apiData._id}
                />
                : <LoadingAnim/>
                
            }
        </div>
        <Sidebar/>
    </>;
}

export default App;
